@import url('https://fonts.googleapis.com/css2? family = Sarabun: ital, wght @ 0,100; 0,200; 0,300; 0,400; 0,500; 0,600; 0,700; 0,800; 1,100; 1,200; 1,300; 1.400; 1.500; 1.600; 1.700; 1.800 & display = swap ');
*{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Sarabun', sans-serif;
}

.container{
  max-width: 800px;
  margin: 0 auto;
}

h1{
  color: #ebd8ce;
}

#informacion{
  display: flex;
  flex-direction: row;
}

.contacto{
  border-right: 3px solid #ebd8ce;
  padding: 10px 30px;
  flex-basis: 40%;
}

p{
  text-align: justify;
}

.link{
  text-decoration: none;
  background-color: #fff;
  border-radius: 25px;
  max-width: 160px;
  height: 25px;
  padding: 2px;
  margin-bottom: 5px;
  border: 1px solid #867870;
  color: #867870;
}