progress[value]{
    width: 90%;
    height: 12px;
    -webkit-appearance: none;
            appearance: none;
    color: #ebd8ce;
}

progress::-moz-progress-bar {background-color: #fefefd;}
progress::-webkit-progress-bar {background-color: #888;}


